import stickySlot from '../../components/stickySlot'
export default {
      name: "scroll2",
    data () {
        return {
          headerData: [],
          footerData: []
        }
    },
  components: {
    stickySlot
  },
  created() {
    // 使用假数据撑满屏幕
    for (let i = 0; i <= 5; i++) {
      this.headerData.push('第' + i + '条数据')
    };
    for (let i = 20; i < 80; i++) {
      this.footerData.push('第' + i + '条数据')
    }
  }

}



